//fv.scss
@use "../../setting/setting" as f;

.dailyFood__topImg {
	width: 100vw;
}

.dailyFood__contents {
	background-color: #000000;
	padding: 135px 0 85px;
	@media (max-width: 600px) {
		padding: 80px 0 40px;
             }
}

.dailyFood__title {
	color: f.$white-color;
	text-align: center;
}



.dailyFood__contentsImgBox {
	display: grid;
	grid-template-areas:
		"a a a a . . . . c c c c"
		"a a a a . . . . c c c c"
		"a a a a . . b b c c c c"
		"d d d d e e . f f f f f"
		"d d d d e e . f f f f f"
		"d d d d . . . f f f f f";
		@media (max-width: 1000px) {
			grid-template-areas:
		". . c c c"
		". . c c c"
		". . . b b"
		"a a a . ."
		"a a a . ."
		". . f f f"
		". . f f f"
		". . . e e"
		"d d d . . "
		"d d d . . ";
		margin-top: 65px;
				 }
}

.dailyFood__Item {
	figure {
		position: relative;
		figcaption {
			color: f.$white-color;
			font-size: 1.8rem;
			position: absolute;
			@media (max-width: 600px) {
				font-size: 1.5rem;
					 }
		}
	}
}

.dailyFood__Item1 {
	grid-area: a;
	transform: translateX(10%);
	@media (max-width: 1000px) {
		transform: translateX(5%);
             }
			 img{
				@media (max-width: 1000px) {
					transform: rotate(-15deg);
						 }
						}
	figcaption {
		bottom: 0;
		left: 20%;
	}
}
.dailyFood__Item2 {
	grid-area: b;
	@media (max-width: 1000px) {
		transform: translate(-20%, 50%);
		width: 90%;
             }
	figcaption {
		bottom: -15%;
		left: 0;
	}
}
.dailyFood__Item3 {
	grid-area: c;
	transform: translateY(-15%);
	@media (max-width: 1000px) {
		transform: translateY(0);
             }
	figcaption {
		bottom: -10%;
		left: 35%;
		@media (max-width: 1000px) {
			bottom: 0;
			left: -50%;
				 }
	}
}
.dailyFood__Item4 {
	grid-area: d;

	figcaption {
		bottom: 10%;
		right: 0%;
		@media (max-width: 1000px) {
			right: -40%;
				 }
	}
}
.dailyFood__Item5 {
	grid-area: e;
	@media (max-width: 1000px) {
		transform: translate(-20%, 20%);
		width: 90%;
             }
	figcaption {
		bottom: -20%;
		left: 25%;
	}
}
.dailyFood__Item6 {
	grid-area: f;
	img{
		@media (max-width: 1000px) {
			transform: rotate(-35deg);
				 }
				}
	figcaption {
		bottom: 20%;
		left: 0;
		@media (max-width: 1000px) {
			left: -50%;
				 }
	}
}

.dailyFood__bottomText {
	margin-top: 80px;
	color: f.$white-color;
	font-size: 3.8rem;
	line-height: 1.5;
	text-align: center;
	@media (max-width: 1000px) {
		padding: 0 20px;
			 }
	@media (max-width: 600px) {
		font-size: 1.8rem;
		text-align: left;
			 }
}
