//fv.scss
@use "../../setting/setting" as f;

.poductJoubiKisho {
	background-image: url(/img/product/item-back.jpg);
	position: relative;
	overflow: hidden;
}

.poductJoubiKisho__gold {
	position: absolute;
}
.poductJoubiKisho__gold1 {
	width: 450px;
	top: 185px;
	right: -200px;
    @media (max-width: 600px) {
        width: 230px;
        top: 20px;
        right: -90px;
             }
}
.poductJoubiKisho__gold2 {
	width: 450px;
	top: 680px;
	left: -225px;
    @media (max-width: 600px) {
        width: 230px;
        top: 670px;
        left: -70px;
             }
}
.poductJoubiKisho__gold3 {
	width: 450px;
	top: 1115px;
	right: -200px;
    @media (max-width: 600px) {
        width: 230px;
        top: 1100px;
        right: -90px;
             }
}
.poductJoubiKisho__gold4 {
	width: 270px;
	top: 1450px;
	left: 0px;
    @media (max-width: 600px) {
        width: 110px;
        top: 1280px;
        left: 0px;
             }
}
.poductJoubiKisho__gold5 {
	width: 310px;
	bottom: 0;
	right: 0px;
    @media (max-width: 600px) {
     width: 250px;
             }
}

.poductJoubiKisho__inner {
	padding: 100px 32px 125px;
	margin: 0 auto;
	position: relative;
	z-index: 2;
    @media screen and (max-width: 600px) {
        padding: 100px 0 80px;
        position: relative;
        overflow-y: hidden;
    }
}

.poductJoubiKisho__titleBox {
	position: relative;
	svg {
		width: 125px;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

.poductJoubiKisho__title {
	color: f.$white-color;
	text-align: center;
	font-weight: 400;
	position: relative;
	display: block;
	width: 300px;
	margin: 0 auto;
    @media (max-width: 600px) {
        font-size: 2rem;
        width: 220px;
    }
	&::before {
		position: absolute;
		content: "";
		top: 50%;
		left: 0;
		width: 40px;
		height: 1px;
		background-color: f.$white-color;
	}
	&::after {
		position: absolute;
		content: "";
		top: 50%;
		right: 0;
		width: 40px;
		height: 1px;
		background-color: f.$white-color;
	}
}

.poductJoubiKisho__joubiContents {
	margin: 90px auto 0;
	max-width: 1200px;
	display: grid;
	column-gap: 50px;
	// grid-template-columns: repeat(12, 1fr);
	// grid-template-rows: repeat(2, 1fr);
	grid-template-areas:
		". . . a a a a a a a . . ."
		"b b b b b b . c c c c c c ";
	row-gap: 100px;
    @media (max-width: 1300px) {
		width: 90%;
    }
    @media (max-width: 1000px) {
        grid-template-areas:
		"a"
		"b"
        "c";
    }
}
.poductJoubiKisho__joubiItem {
	color: f.$white-color;
	figure {
		display: flex;
		column-gap: 15px;
		width: 100%;
        img{
			@media (max-width: 1200px) {
				width: 85%;
					 }
        }
		figcaption {
			font-size: 3.6rem;
			writing-mode: vertical-rl;
            @media (max-width: 1000px) {
              font-size: 1.8rem;
                   }
		}
	}
	p {
		font-size: 2rem;
		margin-top: 32px;
        @media screen and (max-width: 600px) {
        	font-size: 1.6rem;
            line-height: 2.5;
        }
	}
}

.poductJoubiKisho__joubiItem1 {
	grid-area: a;
}
.poductJoubiKisho__joubiItem2 {
	grid-area: b;
    figure{
        @media (max-width: 1000px) {
     flex-direction: row-reverse;
        }
    }
}
.poductJoubiKisho__joubiItem3 {
	grid-area: c;
}

.poductJoubiKisho__kishoBox {
	margin-top: 180px;
}


.poductJoubiKisho{
    .st0{
        opacity: 0.15;
    }
}
