//fv.scss
@use "../../setting/setting" as f;

.gift {
	background: url(../img/product/gift-back.jpg) center center / cover no-repeat;
	position: relative;
	padding: 130px 0 150px;
	@media screen and (max-width: 600px) {
		background: url(../img/product/gift-back-sp.jpg) center center / cover
			no-repeat;
		position: relative;
		padding: 65px 0;
	}
}

.gift__inner {
	width: 90%;
	margin: 0 auto;
}

.gift__mainImg {
	max-width: 1000px;
	margin: 0 auto;
	display: block;
	position: relative;
	z-index: 1;
	@media (max-width: 600px) {
		margin-top: 45px;
	}
}

.gift__contents {
	max-width: 1200px;
	margin: 0 auto;
	background-color: f.$white-color;
	padding: 325px 20px 100px;
	margin-top: -265px;
	@media (max-width: 600px) {
		background-color: unset;
		padding: 0 0 55px;
		margin-top: 0;
	}
}

.gift__titleBox {
	text-align: center;
	position: relative;
	@media (max-width: 600px) {
		display: none;
	}
	img {
		position: absolute;
		bottom: -50px;
		left: 50%;
		transform: translateX(-50%);
		width: 270px;
	}
}

.gift__titleBox--sp {
	display: none;
	@media (max-width: 600px) {
		display: block;
	}
	img {
		@media (max-width: 600px) {
			width: 110px;
			bottom: -20px;
		}
	}
}

.gift__topText {
	font-size: 2rem;
	max-width: 1000px;
	text-align: center;
	margin: 110px auto 0;
	letter-spacing: 0.13em;
	@media (max-width: 600px) {
		margin-top: 25px;
		line-height: 2;
		font-size: 1.6rem;
		text-align: left;
	}
}

.gift__contentsImgBox {
	display: flex;
	justify-content: space-between;
	max-width: 1000px;
	margin: 85px auto 0;
	width: 90%;
	@media (max-width: 1000px) {
		flex-direction: column;
		align-items: center;
	}
	@media (max-width: 600px) {
		margin: 45px auto 0;
		}
	li{
		@media (max-width: 1000px) {
		display: flex;
		border-bottom:1px solid f.$border-gray ;
		padding: 30px 0 ;
		column-gap: 25px;
		align-items: center;
		}
		&:nth-child(even){
			@media (max-width: 1000px) {
				flex-direction: row-reverse;
				}
				.gift__itemNo{
					@media (max-width: 1000px) {
						transform: translate(-240%, 15%);
						}
				}
		}
	}
}
.gift__itemImgBox{
		display: grid;
		// grid-template-columns: repeat(1 1fr);
		// grid-template-rows: repeat(1 1fr);
		grid-template-areas: "a";
		// justify-items: center;
		&:first-child {
			justify-self: start;
		}
		&:last-child {
			justify-self: end;
		}
}

.gift__itemNo {
	width: 100px;
	position: relative;
	grid-area: a;
	z-index: 3;
	transform: translate(-10%, -10%);
	@media (max-width: 1000px) {
		transform: translate(270%, 15%);
			}
			@media (max-width: 600px) {
			width: 60px;
			display: none;
			}
	&::before {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		color: f.$white-color;
		font-size: 4.6rem;
		@media (max-width: 600px) {
			font-size: 3rem;
			}
	}
}

.gift__itemNo1 {
	&::before {
		content: "1";
	}
}
.gift__itemNo2 {
	&::before {
		content: "2";
	}
}
.gift__itemNo3 {
	&::before {
		content: "3";
	}
}

.gift__itemNo--sp{
	display: none;
	@media (max-width: 600px) {
	display: block;
	transform: translate(0%, 0%) !important;
		}
}

.gift__itemImg {
	width: 250px;
	grid-area: a;
	position: relative;
	z-index: 2;
	@media (max-width: 600px) {
		width: 140px;
	}

}
.gift__itemImgShadow {
	width: 250px;
	height: 250px;
	background-color: f.$gift-back;
	grid-area: a;
	transform: translate(15px, 15px);
	position: relative;
	z-index: 1;
	@media (max-width: 600px) {
		width: 140px;
		height: 140px;
		transform: translate(8px, 8px);
	}
}

.gift__itemText {
	margin-top: 30px;
	text-align: center;
	@media (max-width: 1000px) {
	margin-top: 60px;
	text-align: left;
	flex: 1;
		}
	@media (max-width: 600px) {
	margin-top: 10px !important;
		}
}
