//kodawari.scss
@use "../../setting/setting" as f;

.kodawari {
	position: relative;
}

.kodawari__gold {
	position: absolute;
	right: 0;
	top: -80px;
	width: 400px;

	@media (max-width: 1000px) {
		max-width: 375px;
		top: 280px;
		right: unset;
		transform: translateX(-50%);
		left: 50%;
	}
}

.kodawari__title {
	text-align: center;
	color: f.$white-color;

	@media (max-width: 1000px) {
		margin-bottom: 70px;
	}

}

.kodawari__contentsBox {
	overflow-x: hidden;
}

.kodawari__contentsItem {
	background: url(../img/home/kodawari01-back.jpg) center center / cover
		no-repeat;
	padding: 64px 32px;

	@media (max-width: 1000px) {
		padding: 100px 20px;
	}

	&:nth-child(even) {
		background: url(../img/home/kodawari02-back.jpg) center center / cover
			no-repeat;
		padding: 64px 32px;

		@media (max-width: 1000px) {
			padding: 100px 20px;
		}
	}
}

.kodawari__contentsItem1 {
	padding: 125px 0 0;
	display: block;

	@media (max-width: 1000px) {
		padding: 90px 0 0;
	}
}

.kodawari__contentsItemBox {
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
	// padding: 64px 20px 64px 0;
	max-width: 2200px;
	width: 100%;
	margin: 0 auto;

	@media (max-width: 1000px) {
		flex-direction: column;
		align-items: center;
	}
}

.kodawari__contentsItem1Box {
	display: flex;
	align-items: flex-end;
	justify-content: flex-start;
	max-width: 2200px;
	margin: 0 auto;
	padding: 0 20px 64px 0;

	@media (max-width: 1150px) {
        padding: 60px 20px 64px;
	}
	@media (max-width: 1000px) {
		flex-direction: column;
		align-items: center;
		padding: 0 20px;
	}
}

.kodawari__contentsItemBox--even {
	flex-direction: row-reverse;
	justify-content: flex-start;
	@media (max-width: 1200px) {
	padding-top: 32px;
	}
	@media (max-width: 1100px) {
	padding-top: 40px;
	}
	@media (max-width: 1000px) {
		flex-direction: column;
		align-items: center;
        padding-top: 0px;
	}
}

.kodawari__disc {
	text-align: left;
	color: f.$white-color;
	max-width: 650px;
}

.kodawari__disc1 {
	padding-bottom: 90px;
}
.kodawari__disc2 {
    transform: translateY(-50px);
    @media (max-width: 1000px) {
        transform: translateY(0px);
	}
}

.kodawari__contents1LeftImg {
	max-width: 675px;
	transform: translateX(-100px);

	@media (max-width: 1200px) {
		max-width: 550px;
	}

	@media (max-width: 1000px) {
		width: 70%;
		transform: translateX(-0px);
	}
}

.kodawari__contents1LeftImgOdd {
	transform: translateX(-100px);

	@media (max-width: 1000px) {
		width: 70%;
		transform: translateX(-0px);
	}
}

.kodawari__contents1LeftImgEven {
	transform: translateX(30px);
	max-width: 850px;

	@media (max-width: 1300px) {
		max-width: 700px;
	}
	@media (max-width: 1150px) {
		max-width: 600px;
	}
	@media (max-width: 1100px) {
		max-width: 550px !important;
	}
	@media (max-width: 1000px) {
		width: 100%;
		transform: translateX(30px);
	}
}

.kodawari__titleBox2 {
	display: flex;
	flex-direction: row-reverse;
	padding-bottom: 30px;
	border-bottom: 1px solid f.$white-color;
}

.kodawari__discTitle {
	font-size: 3.8rem;
	padding-bottom: 30px;
	border-bottom: 1px solid f.$white-color;
	position: relative;
	line-height: 1.5;

	@media (max-width: 1000px) {
		font-size: 3rem;
		margin-top: 60px;
		text-align: center;
	}

	@media (max-width: 600px) {
		border-bottom: none;
		font-size: 2.2rem;

		&::before {
			position: absolute;
			content: "";
			background-color: f.$white-color;
			width: 115px;
			height: 1px;
			bottom: 1px;
			left: 50%;
			transform: translateX(-50%);
		}
	}
}

.kodawari__no {
	position: absolute;
	top: -100px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 20px;
	background: url(../img/home/kodawari-title.png) center center / cover
		no-repeat;
	background-position: 0px 8px;
	width: 250px;

	@media (max-width: 1000px) {
		display: none;
	}

	span {
		display: inline-block;
		font-style: italic;
		font-size: 5rem;
	}
}

.kodawari__noEven {
	top: 30px;
	right: -60px;
	@media (max-width: 1250px) {
		top: -100px;
		right: 200px;
	}
    	@media (max-width: 1150px) {
	    right: 150px;
	}
    	@media (max-width: 1100px) {
	    right: 120px !important;
	}
}

.kodawari__discText {
	margin-top: 36px;
	letter-spacing: 0.12em;
}

.kodawari__contentsRight {
	margin-top: 88px;
}

.kodawari__bottomImg {
	width: 100vw;
}

.kodawari__contentsItem3 {
	position: relative;
}

.kodawari__gold2 {
	max-width: 470px;
	position: absolute;
	top: -50px;
	right: 0;

	@media (max-width: 1000px) {
		max-width: 375px;
		top: 160px;
		right: unset;
		left: 50%;
		transform: translateX(-50%);
	}
}

.kodawari__noSp {
	display: none;
	position: static;
	color: f.$white-color;
	margin: 0 auto 40px;
	text-align: center;

	@media (max-width: 1000px) {
		display: block;
		width: 270px;
	}
}

.kodawari__contentsItem {
	position: relative;
}

.kodawari__goldSp {
	position: absolute;
	display: none;

	@media (max-width: 1000px) {
		display: block;
		max-width: 375px;
		top: 100px;
		right: unset;
		left: 50%;
		transform: translateX(-50%);
	}
}
